  width: 25%;
  margin-left: 1.88rem;
}

.about-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
}

/* FEATURES */

.features {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  grid-gap: 1.88rem;
  gap: 1.88rem;
  padding-top: 5rem;
}

.feature-item {
  border-bottom: 4px solid transparent;
  padding: 1.5rem 1.88rem;
  transition: all ease 0.3s;
}

.feature-item:hover {
  box-shadow: 0 10px 30px 0 rgba(0, 0, 0, 10%);
  border-color: #f9004d;
}

.feature-icon {
  width: 60px;
  margin: 0 auto 0.5rem;
}

.feature-icon img {
  width: 100%;
}

.feature-title {
  font-family: Montserrat, sans-serif;
  font-size: 1.13rem;
  font-weight: 600;
  text-align: center;
  margin: 0;
}

/* MEDIA */

@media (max-width: 767px) {
  .about-img {
    display: none;
  }
}

@media (max-width: 540px) {
  .about-text {
    padding: 3rem 2rem;
  }

  .about-title-holder {
    display: block;
    margin-bottom: 1rem;
  }

  .about-title span {
    display: inline;
    padding: 0;
  }

  .about-title {
    margin-bottom: 1rem;
  }
}

